















export default {
	name: 'uni-swipe-action',
	props: {
		isOpened: {
			type: Boolean,
			default: false
		},
		disabled: {
			type: Boolean,
			default: false
		},
		autoClose: {
			type: Boolean,
			default: true
		},
		options: Array
	},
	watch: {
		isOpened(newValue, oldValue) {
			this.isShowBtn = newValue ? true : false;
			this.endMove();
		}
	},
	data() {
		const elId = `Uni_${Math.ceil(Math.random() * 10e5).toString(36)}`
		return {
			elId: elId,
			moveLength: 0,
			isMoving: false,
			direction: '',
			startX: 0,
			startY: 0,
			isShowBtn: false,
			btnGroupWidth: 0
		}
	},















	onReady() {
		let view = uni.createSelectorQuery().select(`#${this.elId}`);
		view.fields({
			size: true
		}, data => {
			this.btnGroupWidth = data.width;
		}).exec();
		if (this.isOpened === true) {
			this.isShowBtn = true;
			this.endMove();
		}
	},

	computed: {
		transformX() {
			return `translateX(${this.moveLength}px)`
		}
	},
	methods: {
		bindClickBtn(item, index) {
			this.$emit('click', {
				text: item.text,
				style: item.style,
				index: index
			})
		},
		bindClickCont(e) {
			if (this.isShowBtn && this.autoClose === true) {
				this.isShowBtn = false;
				this.endMove();
			}
		},
		touchStart(event) {
			this.startX = event.touches[0].pageX;
			this.startY = event.touches[0].pageY;
		},
		touchMove(event) {
			if (this.direction === 'Y' || this.disabled === true) {
				return;
			}
			var moveY = event.touches[0].pageY - this.startY,
				moveX = event.touches[0].pageX - this.startX;
			if (!this.isMoving && Math.abs(moveY) > Math.abs(moveX)) { //纵向滑动
				this.direction = 'Y';
				return;
			}
			this.direction = moveX > 0 ? 'right' : 'left';
			this.isMoving = true;
		},
		touchEnd(event) {
			this.isMoving = false;
			if (this.direction !== 'right' && this.direction !== 'left') {
				this.direction = '';
				return;
			}
			if (this.direction == 'right') {
				this.isShowBtn = false
			} else {
				this.isShowBtn = true
			}
			this.endMove()
		},
		endMove() {
			if (this.direction === 'Y' || this.disabled === true) {
				this.direction = '';
				return;
			}
			if (this.isShowBtn) {
				this.moveLength = -this.btnGroupWidth;
				this.$emit('opened');
			} else {
				this.moveLength = 0;
				this.$emit('closed');
			}
			this.direction = '';
		}
	}
}
